<template>
	<swiper ref="mySwiper" :options="swiperOptions">
		<swiper-slide v-for="f in fileList">
			<img :src="f.url" style="width: 1200px;"/>
		</swiper-slide>
		<div class="swiper-pagination" slot="pagination"></div>
	</swiper>
</template>

<script>
	import {
		mapMutations,
		mapState
	} from 'vuex';
	import {
		Swiper,
		SwiperSlide,
		directive
	} from 'vue-awesome-swiper'
	// import style (<= Swiper 5.x)
	import 'swiper/css/swiper.css'

	export default {
		components: {
			Swiper,
			SwiperSlide
		},
		computed: {
			...mapState(['userInfo', 'requestPath', 'picRequestPath'])
		},
		directives: {
			swiper: directive
		},
		async created() {
			this.getCarousel();
		},
		methods: {
			async getCarousel() {
				let {
					data
				} = await this.$get(this.$store.state.requestPath + '/admin/getCarousel');
				let filename = data.data;
				this.fileList = [];
				for (var i = 0; i < filename.length; i++) {
					let file = {
						uid: -(i + 1),
						name: filename[i],
						status: 'done',
						'url': this.picRequestPath + 'carousel/' + filename[i]
					};
					this.fileList.push(file);
				}
			},
		},
		data() {
			return {
				fileList: [],
				swiperOptions: {
					loop: true,
					autoplay: {
						delay: 1000
					},
					pagination: {
						el: '.swiper-pagination'
					},
					// Some Swiper option/callback...
				},
			};
		},
	};
</script>

<style lang="scss">
	.swiper-container {
		height: 450px;
		width: 1300px;

		img {
			width: 100%;
			height: 100%;
		}
	}
</style>
