<template>
	<a-layout-content :style="{ padding: '0 80px', marginTop: '8px' }">		
		<div :style="{ textAlign: 'center', margin: '8px 0 0 20px', padding: '24px'}">
			<Mswiper></Mswiper>
		</div>
		<div v-if = "userInfo">
			<a-divider orientation="left">
			  
			</a-divider>
			<div :style="{ padding: '24px 0px 50px 100px', minHeight: '500px' , textAlign: 'center'}">
				<MovieCard :movies="movieList1" ></MovieCard>
			</div>
		</div>
		<div>
			<a-divider orientation="left">
			  
			</a-divider>
			<div :style="{ padding: '24px 0px 50px 100px', minHeight: '500px' , textAlign: 'center'}">
				<MovieCard :movies="movieList2"></MovieCard>
			</div>
		</div>
		<div v-if = "!userInfo">
			<a-divider orientation="left">
			  
			</a-divider>
			<div :style="{ padding: '24px 0px 50px 100px', minHeight: '500px' , textAlign: 'center'}">
				<p><a href="/"></a></p>
			</div>
		</div>		
	</a-layout-content>
</template>

<script>
	import Mswiper from './Mswiper.vue'
	import MovieCard from './MovieCard.vue'
	import { mapMutations, mapState } from "vuex";
	export default {
		data(){
			return {
				movieList1 : null,
				movieList2 : null,
			}
		},
		async created(){
			this.rcm1();
			this.rcm2();
		},
		components: {
			Mswiper,
			MovieCard
		},
		computed:{
			...mapState(['userInfo','requestPath'])
		},	
		methods: {
			async rcm1(){
				if (this.$store.state.userInfo){
					let {data} = await this.$get(this.$store.state.requestPath + 'rcmmovie/' + this.$store.state.userInfo.uid);
					this.movieList1 = data
				}else{
					console.log("userInfo not found");
				}	
			},
			async rcm2(){
				if (this.$store.state.userInfo){
					let {data} = await this.$get(this.$store.state.requestPath + 'hotmovie/' + this.$store.state.userInfo.uid);
					this.movieList2 = data
				}else{
					console.log("userInfo not found");
				}	
			}
		},
	}
</script>

<style lang="scss" scoped>
</style>
