<template>
	<div style="padding: 5px;" >
		<a-row :gutter="[24,24]" v-if = "movies && userInfo">
			<a-col :span="6" v-for="movie in movies">
				<a-card hoverable :bordered="false" style="width: 240px">
					<img slot="cover" :alt="movie.name" :src="picRequestPath + 'poster/' + movie.pic" @click="toMovie(movie.mid)" />
					<a-card-meta :title="movie.name" id="card">
					</a-card-meta>
				</a-card>
			</a-col>
		</a-row>
	</div>
</template>

<script>
	import { mapMutations, mapState } from "vuex";
	export default {
		props: ['movies'],
		computed:{
			...mapState(['userInfo','requestPath','picRequestPath'])
		},
		methods: {
			toMovie(id) {
				this.$router.push({path:'/home/movie_detail', query:{'mid':id}})
			}
		},

	}
</script>

<style lang="scss">
	#card {
		margin: -12px 0px -25px 0;
		font-style: italic;
		text-align: center;
	}
</style>
